@import "src/styles/global.scss"; @import "portal/global.scss";





































































































.demo-scroll {
    .demo-scroll-wrapper {
        background-color: #f2f2f2;
        width: 100px;
        height: 100px;
        display: inline-block;
    }

    .demo-scroll-box {
        display: inline-block;
        width: 200px;
        height: 200px;
        background-color: #7f8694;
    }

    .demo-scroll-box-item {
        background-color: #1076ca;
        color: white;
        height: 44px;
        line-height: 44px;
        margin-bottom: 12px;
    }
}
